@import '../../../utils/constants.scss';

@media (max-width: 1000px) {
  .diplomas {
    width: 650px;
    height: 500px;
    left: calc((100% - 650px) / 2);
  }

  .diplomas-item {
    object-fit: contain;
  }
}

@media (max-width: 700px) {
  .diplomas {
    width: 410px;
    height: 330px;
    left: calc((100% - 410px) / 2);
  }

  .diplomas__close {
    top: 70px;
    right: 30px;
    width: 18px;
    padding: 3px;
  }
}

@media (max-width: 410px) {
  .diplomas {
    width: 280px;
    height: 230px;
    left: calc((100% - 280px) / 2);
  }

  .diplomas__close {
    top: 30px;
    width: 14px;
  }
}
